<style type="text/css" scoped>
.kv_block {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  padding-top: 4.3%;
  box-sizing: border-box;
  overflow: hidden;
  background: url(../assets/cover/kv_bg.jpg) no-repeat top center;
}

.kv_main {
  width: 11.4rem;
  margin: 0 auto 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: scale(.9) translateY(-50%);
  transform: scale(.9) translateY(-50%);
  transform-origin: top;
}
.kv_pic {
  width: 100%;
  height: 5.25rem;
  margin: .8rem auto 0;
  position: relative;
  /*transform: scale(.95);*/
  /*-webkit-transform: scale(.95);*/
}
.zero{
  width: 1.8rem;
  height: 1.8rem;
  position: absolute;
  top: 42%;
  left: 10%;
}
.zero img{
  float: left;
  margin-left: 8%;
}
.zero_text {
  position: absolute;
  width: 80%;
  left: 26%;
  top: 9%;
}
.zero_text p{
  color: #000000;
  font-size: .16rem;
  text-align: left;
  line-height: .3rem;
  white-space: nowrap;
}
.zero_text .en{
  font-size: .12rem;
  margin-top: .08rem;
  line-height: .18rem;
}
.one{
  position: absolute;
  right: 12%;
  top: 80%;
}
.kv_sloagn{
  margin-top: .5rem;
}
.kv_sloagn p{
  margin-top: .15rem;
  color: #000000;
  font-size: .24rem;
  font-weight: lighter;
  opacity: .6;
}
.slogan_2{
  margin-top: .16rem;
}
.begin_wrap{
  margin-top: .65rem;
}
.begin_wrap p{
  color: #cf393a;
  font-size: .18rem;
  opacity: .3;
  animation: twinkling 1.8s infinite ease-in-out alternate;
}
.begin_wrap a{
  width: .64rem;
  height: .7rem;
  background: url(../assets/cover/begin.png) no-repeat top center/100% auto;
  display: block;
  margin: 0 auto;
  transition: all .2s ease;
}
.begin_wrap a:hover{
  transform: scale(1.1);
}
.begin_wrap a:active{
   transform: scale(.8);
}
.kv_pic img{
  width: 11.4rem;
  height: 5.25rem
}

.fadeIn{
  animation-duration: 1.5s;
}
.fadeInDown{
  animation-duration: 1.8s;
  animation-delay: .4s
}
.fadeInUp{
  animation-delay: .6s
}


.fadeInUpAppear {
  transform: translate(0, 5rem);
  -webkit-transform: translate(0, 5rem);
  opacity: 0;
  /*transition: all 3s ease;*/
}
.fadeInUp2 {
  transform: translate(0, 0);
  opacity: 1;
  transition: all 1.5s .5s ease;
}



@keyframes twinkling{
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity .5s;
}

.fade-enter,
.fade-leave-to

/* .fade-leave-active below version 2.1.8 */
  {
  opacity: 0;
}

@media screen and (max-height: 700px) {
  .kv_main{
    transform: scale(.83) translateY(-50%);
  }
  .kv_pic{
    margin-top: 1.1rem;
  }
}


</style>
<template>
  <transition name="fade">
    <div class="kv_block zoom">
      <div class="kv_main">
        <transition appear appear-active-class="animated fadeIn">
          <div class="kv_pic">
            <img src="../assets/cover/kv_img.png" height="525" width="1140">
          </div>
        </transition>
          <div class="kv_sloagn">
            <transition appear appear-active-class="animated fadeInUp">
              <div class="kv_slogan_txt">
                <p>智能设计, 将设计知识与创意转化为数据,让人们用最简单方式, 获得更好的设计</p>
              </div>
            </transition>
            <transition appear appear-class="fadeInUpAppear" appear-to-class="fadeInUp2">
              <div class="begin_wrap">
                <a href="javascript:;" class="begin_btn" @click="enter_choose"></a>
                <p>点 击 开 始</p>
              </div>
            </transition>
          </div>
      </div>
	</div>
  </transition>
</template>
<script type="text/javascript">
import {mapState, mapGetters, mapMutations} from 'vuex' //引入state，getters，mutations
export default {
  name: 'cover',
  props: {
    // msg: String //例子
  },
  data: function() {
    return {
    }
  },
  created: function() {
    
  },
  computed: {
    ...mapState(['isLogin']),
  },
  methods: {
    ...mapMutations(['setAnswerIndex']),
    // enter_choose: function() {
    //   this.$router.push('inputMsg/q1');
    //   this.setAnswerIndex(1)
    // },
    enter_choose: function() {
      if(this.isLogin) {
        this.$router.push('inputMsg/q1');
        this.setAnswerIndex(1)
      }else {
        
        this.openLogin()
      }
    }
  },
};

</script>
